/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap");

/* Apply global styles */
body {
  font-family: 'Oswald', sans-serif;
  margin: 0;
  color: #000;
}

/* Responsive Styles */
.App {
  text-align: center;
  overflow: hidden; /* Prevent scrolling */
}

.home-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}

.home-banner-container {
  position: relative;
  display: flex;
  flex: 1;
  overflow: visible;
}

.home-bannerImage-container {
  position: absolute;
  top: -100px;
  right: -170px;
  z-index: -2;
  max-width: 700px;
  overflow: visible;
}

.home-image-section {
  max-width: 100%;
}

.home-text-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem;
}

/* Main Heading Styles */
h1 {
  font-size: 38px;
  text-transform: uppercase;
  line-height: 1;
  background: linear-gradient(blue, black);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  margin-bottom: 1rem;
}

/* Button Styles */
.secondary-button {
  padding: 1rem 2.5rem;
  background-color: blue;
  outline: none;
  border: none;
  border-radius: 5rem;
  font-size: 1.1rem;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none; /* Added to remove default link styling */
}

.secondary-button svg {
  margin-left: 0.75rem;
  font-size: 1.5rem;
}

.secondary-button:hover {
  background-color: rgb(3, 3, 163);
  box-shadow: 0 0 10px black;
}

.secondary-button:active{
  transform: scale(1.015) translateY(0.3vh);
  transform-origin: left;
}

