/* Additional Styles */
.container {
  width: 100%;
}

/* Media Query for Larger Screens */
@media (min-width: 768px) {
  .container {
    width: 1100px;
    margin: 0 auto; /* Center the container */
  }

  h1 {
    font-size: 58px;
  }

  .home-text-section {
    padding: 4rem;
  }
}
